import { RouterProvider, createBrowserRouter } from "react-router";
import { useEffect, useState, Suspense } from "react";
import { generateRoutes } from "./utils/generatesRoutes";
import { useDispatch, useSelector } from "react-redux";
import { routes } from "./router";
import { Spin } from "antd";
import { getMenu } from './api/users';
import { setMenu } from "./store/login/authSlice";

function App() {
  const [routerss, setRouter] = useState<any>(null)
  const dispatch = useDispatch()
  const { token } = useSelector((state: any) => state.authSlice)

  useEffect(() => {
    async function loadData() {
      const { data } = await getMenu()
      if (data.length) {
        dispatch(setMenu(data))
        const routers = generateRoutes(data) // 
        const myRoutes = [...routes]

        myRoutes[0].children = routers
        myRoutes[0].children[0].index = true//index index

        const router = createBrowserRouter(myRoutes, { basename: "/react-project" })
        setRouter(router)
      } else {
        const router = createBrowserRouter(routes)
        setRouter(router)
      }
    }
    loadData()
  }, [token])

  if (routerss) {
    return (
      <div className="App">
        <Suspense fallback={<Spin></Spin>}>
          <RouterProvider router={routerss} ></RouterProvider>
        </Suspense>
      </div>
    );
  } else {
    return <Spin></Spin>
  }
}

export default App;
